.card{position:relative;display:flex;flex-direction:column;width:100%;height:100%;color:rgba(255,232,31,0.8);text-decoration:none;word-wrap:break-word;background-color:#32383e;background-color:color 0.2s ease-in;background-clip:border-box}.card:hover{color:rgba(255,232,31,0.8);text-decoration:none;background-color:rgba(50,56,62,0.5)}.card:focus{outline:none;box-shadow:0 0 10px rgba(255,232,31,0.8)}.card-body{flex:1 1 auto;padding:1.25rem}

.menu,.pagination{display:flex;padding:0;list-style:none}.menu__item,.pagination__item{padding-right:16px;padding-left:0}.menu__item:last-child,.pagination__item:last-child{padding-right:0}.pagination{display:flex;justify-content:space-between;margin:4px 0}.pagination__item{display:flex;align-items:center;padding:0;color:#ffe81f;font-size:1rem;text-transform:uppercase;background-color:transparent;border:0;transition:color 0.2s ease-in}.pagination__item:not([disabled]):hover{color:rgba(255,232,31,0.7);cursor:pointer}.pagination__item [disabled]{color:rgba(255,232,31,0.4)}.link,.btn,.sorter{display:inline-block;padding:8px;color:#ffe81f;font-size:1rem;text-transform:uppercase;text-decoration:none;background-color:transparent;border-bottom:1px solid transparent;transition:color 0.2s ease-in, border-bottom 0.2s ease-in}.link:not([disabled]):hover,.btn:not([disabled]):hover,.sorter:not([disabled]):hover{color:rgba(255,232,31,0.7);border-bottom:1px solid #ffe81f;cursor:pointer}.link[disabled],.btn[disabled],.sorter[disabled]{color:rgba(255,232,31,0.4)}.link--character,.btn--character,.sorter--character{padding:0;line-height:1.6em;text-transform:none;border-bottom:0}.link--character:not([disabled]):hover,.btn--character:not([disabled]):hover,.sorter--character:not([disabled]):hover{border-bottom:0}.link--active{border-bottom:1px solid #ffe81f}.sorter{padding-bottom:0;border:0}.sorter:not([disabled]):hover{border-bottom:0;cursor:pointer}.sorter .option{padding:4px 12px;color:#ffe81f;font-size:1rem;text-decoration:none;background:#484e55;border:1px solid rgba(0,0,0,0.6)}

